import React, { useRef } from 'react';
import useExpired from '../lib/hooks/useExpired';

/**
 * @param sec 
 * @returns expiredDate  + sec, ex: 2020-04-10T23:59:59+08:00
 */
export const getExpiredDate = (sec: number) => {
  const n = new Date();
  const offset = -n.getTimezoneOffset() / 60;
  n.setHours(n.getHours() + offset);
  n.setSeconds(n.getSeconds() + sec);
  const offsetString =
    // eslint-disable-next-line no-nested-ternary
    offset >= 0
      ? offset <= 9
        ? `+0${offset.toString()}:00`
        : `+${offset.toString()}:00`
      : offset >= -9
      ? `-0${offset.toString()}:00`
      : `-${offset.toString()}:00`;

  const expiredDate = n.toISOString().split('.')[0] + offsetString;

  return expiredDate;
};

const Expired = () => {
  const expiredDate = useRef(getExpiredDate(10));
  const isExpired = useExpired(expiredDate.current);
  return (
    <div>
      <span>
        (expiredDate)  playground
        +10(expiredDate)(isExpired)
        false  true
        <br />
        (expiredDate)useExpired
        playground 
      </span>
      <br />
      <br />
      <span>(expiredDate): {expiredDate.current}</span>
      <br />
      <span>(isExpired): {isExpired.toString()}</span>
      <br />
    </div>
  );
};

export default React.memo(Expired);
