import { useState, useEffect } from 'react';

/**
 *  startRender  true 
 * @returns startRender
 */
const useStartRender = () => {
  const [startRender, setStartRender] = useState(false);
  useEffect(() => {
    setStartRender(true);
  }, []);
  return startRender;
};

export default useStartRender;
