import React, { useMemo } from 'react';
import useKeyboard from '../lib/hooks/useKeyboard';
import { ISetting } from '../lib/types';
import { EventTypes } from '../lib/enums';
import { qs, getKeyboardSettings, globalThis } from '../lib/utils';
import useStartRender from '../lib/hooks/useStartRender';

const renderPage = () => {
  const { page } = qs();
  let PageComponent;
  switch (page) {
    case '9': {
      PageComponent = () => <div>page=9</div>;
      break;
    }
    case '8': {
      PageComponent = () => <div>page=8</div>;
      break;
    }
    case '7': {
      PageComponent = () => <div>page=7</div>;
      break;
    }
    case '6': {
      PageComponent = () => <div>page=6</div>;
      break;
    }
    case '5': {
      PageComponent = () => <div>page=5</div>;
      break;
    }
    case '4': {
      PageComponent = () => <div>page=4</div>;
      break;
    }
    case '3': {
      PageComponent = () => <div>page=3</div>;
      break;
    }
    case '2': {
      PageComponent = () => (
        <div>
          page=2 <br />
          page=2  offlineRound  page  offlineRound 
          page=2 offlineRound  button
        </div>
      );
      break;
    }
    case '1':
    default: {
      PageComponent = () => <div>page=1</div>;
      break;
    }
  }

  return <PageComponent />;
};

const Keyboard = () => {
  const settings = useMemo(() => {
    const defaultKeyboardSettings: ISetting[] = getKeyboardSettings(1, 9);
    const customKeyboardSettings = [
      {
        type: EventTypes.CUSTOM,
        key: 'ArrowDown',
        fn: () => {
          const search = qs();
          const pageInt = parseInt(search.page as string, 10);
          window.scrollTo(0, 0);
          const query = {
            ...search,
            page: pageInt > 1 ? `${pageInt - 1}` : '1',
          };
          const queryPath = Object.entries(query).map(
            ([key, value]) => `${key}=${value}`,
          );
          const nextLocation = `${
            globalThis.location.pathname
          }?${queryPath.join('&')}`;
          globalThis.location.href = nextLocation;
        },
      },
      {
        type: EventTypes.CUSTOM,
        key: 'ArrowUp',
        fn: () => {
          const search = qs();
          window.scrollTo(0, 0);
          const query = {
            ...search,
            page:
              !search.page || parseInt(search.page as string, 10) < 9
                ? `${parseInt((search.page || 1) as string, 10) + 1}`
                : search.page,
          };
          const queryPath = Object.entries(query).map(
            ([key, value]) => `${key}=${value}`,
          );
          const nextLocation = `${
            globalThis.location.pathname
          }?${queryPath.join('&')}`;
          globalThis.location.href = nextLocation;
        },
      },
    ];

    const finalKeyboardSettings = [
      ...defaultKeyboardSettings,
      ...customKeyboardSettings,
    ];
    return finalKeyboardSettings;
  }, []);

  useKeyboard(settings);
  const startRender = useStartRender();

  return !startRender ? (
    <div>Loading...</div>
  ) : (
    <div>
      offlineRoundKeyboard
      <br />
      1~9(-1)(+1)1~9
      <br />
      (-1)(+1)
      <br />
      fn
      <br />
      playground(-1)(+1)fn1~9
      <br />
      (-1)(+1)
      <br />
      
      <br />
      <br />
      {renderPage()}
    </div>
  );
};

export default Keyboard;
